/* === Hero (final, single source of truth) === */
.hero {
  position: relative;
  background: transparent;
  color: #fff;
  min-height: clamp(520px, 62vh, 720px);
  height: 85vh;
  overflow: hidden;
}

.hero__inner {
  position: relative;
  max-width: 1280px;
  margin: 0 auto;
  min-height: 62vh;
  height: clamp(520px, 62vh, 720px);
}

/* top divider line */
.hero__rule {
  position: absolute;
  top: 14px;
  left: 3%;
  right: 3%;
  height: 3px;
  border-radius: 2px;
  background: rgba(255, 255, 255, 0.65);
  pointer-events: none;
  z-index: 1;
}

/* logo badge (white circle) */
.hero__badge {
  position: absolute;
  left: clamp(18px, 3.6vw, 48px);
  top: clamp(26px, 6vh, 72px);
  width: clamp(200px, 24vw, 320px);
  aspect-ratio: 1 / 1;
  z-index: 3;
}
.hero__badgeCircle {
  width: 100%;
  height: 100%;
  border-radius: 9999px;
  background: #fff;
  display: grid;
  place-items: center;
  box-shadow: 0 18px 40px rgba(0, 0, 0, 0.28);
  /* small nudge to visually center logo inside circle vs page edge */
  margin-left: 14%;
  position: relative;
}
.hero__badgeCircle::after {
  content: "";
  position: absolute;
  inset: 10px;
  border-radius: 9999px;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.06);
  pointer-events: none;
}
.hero__badgeLogo {
  width: 78%;
  max-width: 86%;
  height: auto;
  object-fit: contain;
}

/* trucks image (decorative, right side) */
.hero__trucks {
  position: absolute;
  right: 0;
  bottom: 20vh;
  width: 58vw;
  max-width: 860px;
  z-index: 1;
  filter: drop-shadow(0 18px 24px rgba(0, 0, 0, 0.35));
  pointer-events: none;
}
.hero__trucks img {
  width: 100%;
  height: auto;
  object-fit: contain;
}

/* brand & subhead lockup (bottom-left) */
.hero__title {
  position: absolute;
  left: clamp(22px, 3.6vw, 52px);
  bottom: 2vh;
  margin: 0;
  z-index: 3;
  max-width: min(520px, 42vw);
  line-height: 1.04;
  text-shadow: 0 4px 14px rgba(0, 0, 0, 0.45);
}
.hero__brand {
  display: block;
  font-weight: 900;
  font-size: 5.3vw;
  letter-spacing: 0.5px;
  font-family: Georgia, "Times New Roman", serif;
}
.hero__sub {
  display: block;
  margin-top: 8px;
  font-weight: 800;
  font-size: 3vw;
  font-family: Georgia, "Times New Roman", serif;
  margin-left: 2%;
}

/* tagline (bottom-right) */
.hero__tagline {
  position: absolute;
  right: clamp(20px, 3.6vw, 44px);
  bottom: clamp(26px, 6vh, 48px);
  z-index: 3;
  max-width: min(48ch, 44vw);
  font-size: clamp(15px, 1.6vw, 20px);
  line-height: 1.35;
  text-shadow: 0 4px 14px rgba(0, 0, 0, 0.45);
}

/* RTL mirroring */
.hero--rtl .hero__badge {
  left: auto;
  right: clamp(18px, 3.6vw, 48px);
}
.hero--rtl .hero__title {
  left: auto;
  right: clamp(22px, 3.6vw, 52px);
  text-align: right;
}
.hero--rtl .hero__tagline {
  right: auto;
  left: clamp(20px, 3.6vw, 44px);
  text-align: right;
}
.hero--rtl .hero__trucks {
  right: auto;
  left: 0;
  transform: scaleX(-1);
}

/* Responsive layout */
@media (max-width: 900px) {
  .hero {
    min-height: 680px;
  }
  .hero__badge {
    left: 50%;
    right: auto;
    top: clamp(20px, 5vh, 40px);
    transform: translateX(-50%);
    width: clamp(180px, 42vw, 280px);
  }
  .hero--rtl .hero__badge {
    left: 50%;
    right: auto;
    transform: translateX(-50%);
  }

  .hero__title {
    left: 50%;
    right: auto;
    transform: translateX(-50%);
    bottom: clamp(160px, 22vh, 200px);
    text-align: center;
    max-width: 90vw;
  }
  .hero--rtl .hero__title {
    text-align: center;
  }

  .hero__tagline {
    left: 50%;
    right: auto;
    transform: translateX(-50%);
    bottom: clamp(24px, 6vh, 40px);
    text-align: center;
    max-width: 90vw;
  }

  .hero__trucks {
    width: 90vw;
    right: 50%;
    left: auto;
    transform: translateX(50%);
  }
  .hero--rtl .hero__trucks {
    left: 50%;
    right: auto;
    transform: translateX(-50%) scaleX(-1);
  }
}
